.icon-desktop:before, .icon-menu-close:before, .icon-menu:before, .icon-mobile:before, .icon-submenu-down:before, .icon-submenu-right:before, .icon-world:before, .nav .nav-submenu>a:after, .nav-button:before, .nav-close:before, .nav>ul>.nav-submenu>a:after, header h1:before {
  font-family: icon-font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-variant: normal;
  font-weight: 400;
  text-decoration: none;
  text-transform: none;
  vertical-align: top
}

.icon-desktop:before {
  content: "\E001"
}

.icon-menu-close:before {
  content: "\E002"
}

.icon-menu:before {
  content: "\E003"
}

.icon-mobile:before {
  content: "\E004"
}

.icon-submenu-down:before {
  content: "\27A4"
}

.icon-submenu-right:before {
  content: "\E006"
}

.icon-world:before {
  content: "\E007"
}

body {
  padding-top: 4.4rem
}

header {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 901;
  padding-left: 10px;
  line-height: 4.4rem;
  background: #a73f2d;
  color: white;
}

header h1 {
  margin: 0;
  font-size: 1.7rem;
  line-height: 4.4rem
}

header h1:before {
  content: "\1F4F1";
  margin-right: 8px
}

/* Barre Menus */

.nav-button, .nav-close {
  position: fixed;
  top: 0;
  width: 2.4rem;
  height: 4.4rem;
  overflow: hidden;
  z-index: 902;
  cursor: pointer;
  text-decoration: none;
  line-height: 4.4rem;
  background: #a73f2d;
  color: white;
}

.nav-button:before, .nav-close:before {
  display: block;
  text-align: center
}

.nav-button {
  display: block;
  right: 1rem;
  font-size: 1.7rem
}

.nav-button:before {
  content: "\2630"
}

.nav-close {
  display: none;
  right: 4rem;
  font-size: 1.9rem
}

.nav-close:before {
  content: "\E002"
}

.nav {
  padding-top: 4.4rem;
  line-height: 4.4rem;
  background: #a73f2d;
  color: white;
}

.nav ul {
  border-radius: 0 0 6px 6px;
  background: #a73f2d;
}

/* Sous menus */

.nav ul ul {
  background: #2f2722
}

.nav ul ul ul {
  background: #382f29
}

.nav ul ul ul ul {
  background: #40362f
}

.nav ul ul ul ul ul {
  background: #493e36
}

.nav li {
  border-top: 1px solid #59544F;
  cursor: pointer
}

.nav li.nav-active>a, .nav li:hover>a {
  color: white;
  background: #493e36;
}

.nav .nav-submenu>ul {
  margin-left: 10px
}

.nav .nav-submenu>a {
  padding-right: 30px !important
}

.nav a, .nav a:active, .nav a:hover, .nav a:visited {
  border-radius: 6px;
  padding: 0 15px 1px;
  color: white;
  text-decoration: none
}

.nav .nav-submenu>a:after {
  position: absolute;
  display: block;
  right: 10px;
  top: 1px;
  content: "\25BC"
}

/* Menus desktop */

@media only screen and (min-width: 960px) {
  ader {
    display: none;
  }
  body {
    padding: 0 !important
  }
  header {
    position: relative;
    display: none;
  }
  header h1:before {
    content: "\E001"
  }
  .nav-button {
    display: none
  }
  .nav {
    padding: 0 !important
  }
  .nav>ul>li {
    border-top: none
  }
  .nav li>ul {
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, .75)
  }
  .nav ul ul ul {
    border-radius: 0 6px 6px
  }
  .nav ul ul ul li:first-child {
    border-top: none
  }
  .nav .nav-left>ul {
    border-radius: 6px 0 6px 6px
  }
  .nav .nav-submenu li {
    min-width: 110px
  }
  .nav .nav-submenu>ul {
    margin: 0
  }
  .nav>ul>.nav-submenu>a:after {
    content: "\25BC"
  }
  .nav .nav-submenu>a:after {
    content: "\25B6"
  }
}
